/*
* Refonte SCSS pour les formulaires Contact Form 7
* - bordures 1px solid $velvet
* - border-radius 2px
* - pas d'outline au focus
* - placeholder 0.9rem
* - utilitaire .form-half (50/50)
* - garde compatibilit avec Bootstrap via slecteurs spcifiques
*/

.wpcf7 {
	max-width: 50rem;
	margin: 0 auto !important;
}
// inherit bootstrap form-control where pertinent
input[type="text"],
input[type="search"],
input[type="url"],
input[type="tel"],
input[type="number"],
input[type="email"],
textarea {
	@extend .form-control;
}

input[type="submit"] {
	@extend .btn;
	@extend .btn-outline-primary;
}

.wpcf7-form {
	// set paragraphs to behave like divs with class .form-group
	p {
		@extend .form-group !optional;
		margin-bottom: $spacer; // Replicates the .form-group functionality in BS5.
	}

	// let labels be 100%
	label {
		width: 100%;
	}
}

// not valid tip for each control
.wpcf7-not-valid-tip {
	color: theme-color("danger");
}

// validation errors ourput bottom of form
.wpcf7-validation-errors {
	color: theme-color("danger");
	border: 1px solid $gray-200;
}

/* --- Input / textarea styling --- */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select,
textarea,
.wc-blocks-components-select__select,
.wpcf7-form-control {
	// width: auto !important;
	font-size: 0.9rem !important;
	color: $black-main !important;
	border: none !important;
	border-radius: 2px !important;
	background-color: rgba($bloom, 0.3) !important;
	background-clip: padding-box;
	box-shadow: none !important;
	outline: none !important;
	transition: background-color 0.15s ease, border-color 0.15s ease,
		box-shadow 0.15s ease;
}
.wpcf7-acceptance {
	background-color: transparent !important;
}
textarea {
	resize: none;
}

/* Focus : haute spcificit pour craser Bootstrap */
// cible inputs qui ont la classe .form-control (Bootstrap)
input.form-control:focus,
textarea.form-control:focus,
select.form-control:focus,
input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="search"]:focus,
input[type="url"]:focus,
select:focus,
textarea:focus,
.wpcf7-form-control:focus {
	background-color: rgba($bloom, 0.5) !important;
	border-color: $velvet !important;
	outline: none !important;
	box-shadow: none !important;
}

/* submit/button */
input[type="submit"],
button,
.wpcf7-submit {
	border-radius: 2px;
	&:focus {
		outline: none !important;
		box-shadow: none !important;
	}
}

/* --- 50/50 fields --- */
.form-half {
	display: inline-block;
	width: calc(50% - 8px);
	box-sizing: border-box;
	vertical-align: top;
}

.form-half + .form-half {
	margin-left: 16px;
}

@media (max-width: 575px) {
	.form-half {
		display: block;
		width: 100%;
		margin-left: 0 !important;
		margin-bottom: 0.75rem;
	}
}

/* --- Placeholder size --- */
input::placeholder,
textarea::placeholder,
select::placeholder {
	font-size: 0.9rem !important;
	color: rgba($black-main, 1) !important;
}

/* vendor fallbacks */
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
	font-size: 0.9rem !important;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
	font-size: 0.9rem !important;
	opacity: 1 !important;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
	font-size: 0.9rem !important;
}
input::-ms-input-placeholder,
textarea::-ms-input-placeholder {
	font-size: 0.9rem !important;
}

/* Formulaire CF7  layout : prnom/nom 50% chacun, autres 100%, bouton centr */
.contact__row--two {
	display: flex;
	gap: 1rem;
	align-items: flex-start;
	flex-wrap: wrap;
}
.field--half {
	flex: 1 1 calc(50% - 8px);
	min-width: 200px;
}
.field {
	width: 100%;
}
.contact__button--center {
	display: flex;
	justify-content: center;
	margin-top: 12px;
	margin-bottom: 24px;
}

// Forcer styles pour les boutons "view-all-link" dans les formulaires CF7
.wpcf7,
form.wpcf7-form {
	/* selector trs spcifique pour surpasser Bootstrap */
	.view-all-link,
	button.view-all-link,
	input[type="submit"].view-all-link,
	.wpcf7-submit.view-all-link {
		display: inline-flex !important;
		align-items: center !important;
		justify-content: center !important;
		padding: 0.75rem 1.5rem !important;
		font-size: 0.95rem !important;
		font-weight: 700 !important;
		text-transform: uppercase !important;
		color: #fff !important;
		background: $black-main !important;
		border: 1px solid #fff !important;
		border-radius: 0 !important;
		cursor: pointer !important;
		line-height: 1 !important;
		text-decoration: none !important;
		box-shadow: none !important;
		transition: all 0.3s !important;
	}

	/* focus/hover states */
	.view-all-link:hover,
	button.view-all-link:hover,
	input[type="submit"].view-all-link:hover,
	.wpcf7-submit.view-all-link:hover {
		color: rgba($black-main, 1) !important;
		background: $bloom !important;
		border-color: $bloom !important;
		outline: none !important;
		box-shadow: none !important;
	}

	.view-all-link:focus,
	button.view-all-link:focus,
	input[type="submit"].view-all-link:focus,
	.wpcf7-submit.view-all-link:focus {
		outline: none !important;
		box-shadow: none !important;
	}
}

/* responsive */
@media (max-width: 600px) {
	.field--half {
		flex-basis: 100%;
	}
}
/* Supprimer les flches natives des input[type="number"] (Chrome, Safari, Edge, Firefox) */
.wpcf7 input[type="number"],
form.cart input[type="number"] {
	-moz-appearance: textfield;
	appearance: textfield;
}

.wpcf7 input[type="number"]::-webkit-outer-spin-button,
.wpcf7 input[type="number"]::-webkit-inner-spin-button,
form.cart input[type="number"]::-webkit-outer-spin-button,
form.cart input[type="number"]::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
.wc-block-cart__submit-button,
.wc-block-components-checkout-place-order-button {
	transition: all 0.2s ease !important;
	@extend .view-all-link;
}
.wc-block-components-totals-coupon__content {
	button[type="submit"] {
		@extend .btn, .btn-velvet;
		border: 1px solid $velvet !important;
		min-height: auto !important;
	}
}
.wc-block-components-totals-coupon__form {
	gap: 0 !important;
}
